import * as api from "../../constantHelper/API";
import axios from "axios";


export const getManageContentAPI = (url) => {
  return api.getHttpRequest(url);
};

export const getHttpRequest = (url) => {
  return api.getHttpRequest(url);
}

export const postHttpRequestAsync = (url, data) => {
  return api.postHttpRequestAsync(url, data);

}

export const axiosConfig = (method, url, data = {}, headers = {}) => {
  return axios({
    method,
    url,
    data,
    headers
  });

}