import * as types from "./manageContentTypes";

const initialState = {
  partnerId: localStorage.getItem('hospital') || "dkkvangiang",
  content: [],
  domainList: [],
  error: ""
};

export default function manageContentReducer(state = initialState, action = {}) {

  switch (action.type) {

    case types.GET_MANAGE_CONTENT:
      return {
        ...state,
        partnerId: action.partnerId
        // ...action.payload
      };
    case types.GET_MANAGE_CONTENT_SUCCESS:
      return {
        ...state,
        content: action.payload
      };
    case types.GET_MANAGE_CONTENT_FAIL:
      return {
        ...state,
        error: 'error'
      };

    // case types.GET_DOMAIN_LIST:
    //   return {
    //     ...state,
    //     partnerId: action.partnerId
    //     ...action.payload
    //   };
    case types.GET_DOMAIN_LIST_SUCCESS:
      return {
        ...state,
        domainList: action.payload
      };
    case types.GET_DOMAIN_LIST_FAIL:
      return {
        ...state,
        error: 'error'
      };


    default:
      return state;
  }
}
