import * as types from "./manageContentTypes";


export const getManageContent = (partnerId) => {
  // console.log(123);
  return {
    type: types.GET_MANAGE_CONTENT,
    partnerId
  };
};

export const getDomainList = (partnerId) => {
  // console.log(123);
  return {
    type: types.GET_DOMAIN_LIST,
    partnerId
  };
};

export const addDomain = (domain) => {
  // console.log(123);
  return {
    type: types.ADD_DOMAIN,
    domain
  };
};

export const editDomain = (domain) => {
  // console.log(123);
  return {
    type: types.EDIT_DOMAIN,
    domain
  };
};

export const deleteDomain = (id) => {
  // console.log(123);
  return {
    type: types.DELETE_DOMAIN,
    id
  };
};