import React from 'react';
import './App.css';
import { Switch, Route, BrowserRouter as Router, HashRouter } from 'react-router-dom';
import { connect } from "react-redux";
import AntMenu from './components/Menu/Menu';
import { checkAuth } from './store/checkAuth/checkAuthActions';
import { Redirect } from "react-router-dom";

// import FormContent from "./Page/FormContent/FormContent";
import routes from './routes';
import history from "./history";

class App extends React.Component {

  componentDidMount = () => {
    this.props.checkAuth()
  }


  showContentMenus = (routes) => {
    var result = null;
    if (routes.length > 0) {
      result = routes.map((route, index) => {
        return (<Route key={index} path={route.path} exact={route.exact} render={route.main} />)
      })
    }

    return <Switch>{result}</Switch>
  }

  render() {
    return (

      <HashRouter history={history}>

        <div className="App">
          <h1>PKH Content Management</h1>
          <AntMenu />
          <br />
          <br />

          {this.showContentMenus(routes)}

        </div>
      </HashRouter>
    );
  }
}



const mapDispatchToProps = dispatch => ({
  checkAuth: () => {
    // console.log('abc')
    dispatch(checkAuth());
  },
});

export default connect(null, mapDispatchToProps)(App);
