import { put, call, fork, all, takeLatest, select } from "redux-saga/effects";
import { message } from 'antd';
import * as api from "./manageContentAPIs";
import * as types from "./manageContentTypes"
import { currentEnv } from "../../configs";




function* getManageContent(action) {

  try {
    const response = yield call(api.getManageContentAPI, `${currentEnv.DOMAIN_URL}/content/${action.partnerId}`);
    if (response.status !== 200 || response.data.error_code)
      throw new Error("Lỗi server!");

    if (response) {
      const { data } = response;
      const payload = data.imageManage ? data.imageManage : []
      yield put({ type: types.GET_MANAGE_CONTENT_SUCCESS, payload });
    }
  } catch (error) {
    yield put({ type: types.GET_MANAGE_CONTENT_FAIL, error });
  }
}
function* watchGetManageContent() {
  yield takeLatest(types.GET_MANAGE_CONTENT, getManageContent);
}

function* getDomainList(action) {

  try {
    const response = yield call(api.getHttpRequest, `${currentEnv.DOMAIN_URL}/domainList/${action.partnerId}`);
    if (response.status !== 200 || response.data.error_code)
      throw new Error("Lỗi server!");

    if (response) {
      const { data } = response;
      // console.log(data)
      // const payload = data.length > 0 ? data[0].domainList : []
      // console.log(payload)
      yield put({ type: types.GET_DOMAIN_LIST_SUCCESS, payload: data });
    }
  } catch (error) {
    yield put({ type: types.GET_DOMAIN_LIST_FAIL, error });
  }
}
function* watchGetDomainList() {
  yield takeLatest(types.GET_DOMAIN_LIST, getDomainList);
}

function* addDomain(action) {
  // console.log(action)
  const partnerId = action.domain.partnerId;
  // const domain = { ...action.domain }
  // console.log(partnerId)
  const data = { ...action.domain }

  try {
    const response = yield call(api.axiosConfig, "post", `${currentEnv.DOMAIN_URL}/domainList`, data, { token: localStorage.getItem('token') });
    // console.log(response)
    if (response.status !== 201)
      throw new Error("Lỗi server!");

    if (response) {
      // const { data } = response;
      // console.log(data)
      // const payload = data.length > 0 ? data[0].domainList : []
      // console.log(payload)
      // yield put({ type: types.ADD_DOMAIN_SUCCESS, payload });
      yield put({ type: types.GET_DOMAIN_LIST, partnerId });
    }
  } catch (error) {
    // console.log(error)
    // console.log(error.response)
    if (error.response.status === 401) {
      message.error(`You are not loggin!`);
      // yield put({ type: 'SET_LOGIN', isLogin: false });
    }
    // yield put({ type: types.ADD_DOMAIN_FAIL, error });
  }
}
function* watchAddDomain() {
  yield takeLatest(types.ADD_DOMAIN, addDomain);
}

