function* deleteDomain(action) {
  // console.log(action)
  const partnerId = yield select(state => state.manageContent.partnerId);
  // const partnerId = action.domain.partnerId;
  // const domain = { ...action.domain }
  // console.log(partnerId)
  const id = action.id

  try {
    const response = yield call(api.axiosConfig, "delete", `${currentEnv.DOMAIN_URL}/domainList/${id}`, {}, { token: localStorage.getItem('token') });
    // console.log(response)
    if (response.status !== 200)
      throw new Error("Lỗi server!");

    if (response) {
      // const { data } = response;
      // console.log(data)
      // const payload = data.length > 0 ? data[0].domainList : []
      // console.log(payload)
      // yield put({ type: types.ADD_DOMAIN_SUCCESS, payload });
      yield put({ type: types.GET_DOMAIN_LIST, partnerId });
    }
  } catch (error) {
    if (error.response.status === 401) {
      message.error(`You are not loggin!`);
    }
    // yield put({ type: types.DELETE_DOMAIN_FAIL, error });
  }
}
function* watchDeleteDomain() {
  yield takeLatest(types.DELETE_DOMAIN, deleteDomain);
}




export default function* root() {
  yield all([
    fork(watchGetManageContent),
    fork(watchGetDomainList),
    fork(watchAddDomain),
    fork(watchEditDomain),
    fork(watchDeleteDomain),
  ]);
}
