import React from 'react';
import { Menu, Row, Col } from 'antd';
import { setLogin } from '../../store/checkAuth/checkAuthActions';
import { connect } from "react-redux";
// import { MailOutlined, AppstoreOutlined, SettingOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';

// const { SubMenu } = Menu;

class AntMenu extends React.Component {
  state = {
    current: '',
  };

  handleClick = e => {
    // console.log('click ', e);
    this.setState({ current: e.key });
  };

  logout = () => {
    // console.log('abc');
    localStorage.setItem('token', "");
    this.props.setLogin(false)
  }

  render() {
    const { current } = this.state;
    // console.log(this.props.isLogin)
    return (
      <Row justify="center">
        <Menu onClick={this.handleClick} selectedKeys={[current]} mode="horizontal">
          <Menu.Item key="hospital-content">
            <Link to="/hospital-content">Hospital Content</Link>
          </Menu.Item>
          <Menu.Item key="app-icon">
            <Link to="/app-icon">App Icon</Link>
          </Menu.Item>
          {!this.props.isLogin ?
            <Menu.Item key="login">
              <Link to="/login">Login</Link>
            </Menu.Item> :
            <Menu.Item key="logout" onClick={this.logout}>
              Logout
            </Menu.Item>}
        </Menu>
      </Row>
    );
  }
}

const mapStateToProps = state => ({
  isLogin: state.checkAuth,
});

const mapDispatchToProps = dispatch => ({
  setLogin: (isLogin) => {
    // console.log('abc')
    dispatch(setLogin(isLogin));
  },
});



export default connect(mapStateToProps, mapDispatchToProps)(AntMenu);