import { put, call, fork, all, takeLatest, select } from "redux-saga/effects";
import * as api from "../../constantHelper/API";
import * as types from "./checkAuthTypes"
import { currentEnv } from "../../configs";




function* checkAuth(action) {
  // console.log(action)
  try {
    // const baseUrl = yield select(state => getBaseUrl(state.hospital));
    const response = yield call(api.postHttpRequestAsync, `${currentEnv.DOMAIN_URL}/checkAuth`, { token: localStorage.getItem('token') });
    if (response.status !== 201) {
      throw new Error("Lỗi server!");
    }

    if (response) {
      const { data } = response;
      // const shortData = data.map((item, index) => {
      //   return { name: item.name, partnerId: item.partnerId }
      // })
      // console.log(data)
      yield put({ type: types.CHECK_AUTH_SUCCESS, payload: data.isLogin });
    }
  } catch (error) {
    yield put({ type: types.CHECK_AUTH_FAIL });
  }
}
function* watchCheckAuth() {
  yield takeLatest(types.CHECK_AUTH, checkAuth);
}




export default function* root() {
  yield all([
    fork(watchCheckAuth),
  ]);
}
