import * as types from "./checkAuthTypes";

const initialState = false;

export default function checkAuthReducer(state = initialState, action = {}) {
  switch (action.type) {
    // case types.CHECK_AUTH:
    //   return state
    case types.CHECK_AUTH_SUCCESS:
      return action.payload
    case types.CHECK_AUTH_FAIL:
      return false
    case types.SET_LOGIN:

      return action.isLogin


    default:
      return state;
  }
}