const mapStateToPropsForm = state => ({
  manageContent: state.manageContent,
});

const mapDispatchToPropsForm = dispatch => ({
  getDomainList: (partnerId) => {
    dispatch(getDomainList(partnerId));
  },
  addDomain: (domain) => {
    dispatch(addDomain(domain));
  },
});

const WrapperForm = connect(mapStateToPropsForm, mapDispatchToPropsForm)(MyFormComponent)



class DomainListTable extends React.Component {

  state = {
    // data: [],
    enviromentValue: "awdwad",
    domainValue: "awdwd",
    editting: 0
  }


  componentDidMount = () => {
    // console.log(this.props.manageContent.partnerId)
    // axios.get(`${currentEnv.DOMAIN_URL}/domainList/${this.props.manageContent.partnerId}`)
    //   .then(response => {
    //     // console.log(response.data);
    //     const data = response.data
    //     if (data.length > 0) {
    //       if (data[0].domainList.length > 0) {
    //         const tableData = data[0].domainList.map((element, index) => {
    //           return {
    //             key: (index + 1).toString(),
    //             domain: element.domain,
    //             enviroment: element.enviroment
    //           }
    //         })
    //         this.setState({
    //           data: tableData
    //         })
    //       }

    //     }

    //     // this.setState({
    //     //   isLogin: response.data.isLogin
    //     // })
    //   }).catch(error => {
    //     console.log(error)
    //   });

    this.props.getDomainList(this.props.manageContent.partnerId)
  }

  UNSAFE_componentWillReceiveProps = (newProps) => {
    // console.log(newProps)
    if (newProps.manageContent.partnerId !== this.props.manageContent.partnerId) {
      // console.log('abc');
      // axios.get(`${currentEnv.DOMAIN_URL}/domainList/${newProps.manageContent.partnerId}`)
      //   .then(response => {
      //     // console.log(response.data);
      //     const data = response.data
      //     if (data.length > 0) {
      //       if (data[0].domainList.length > 0) {
      //         const tableData = data[0].domainList.map((element, index) => {
      //           return {
      //             key: (index + 1).toString(),
      //             domain: element.domain,
      //             enviroment: element.enviroment
      //           }
      //         })
      //         this.setState({
      //           data: tableData
      //         })
      //       } else {
      //         this.setState({
      //           data: []
      //         })
      //       }

      //     } else {
      //       this.setState({
      //         data: []
      //       })
      //     }

      //     // this.setState({
      //     //   isLogin: response.data.isLogin
      //     // })
      //   }).catch(error => {
      //     console.log(error)
      //   });
      this.props.getDomainList(newProps.manageContent.partnerId)

    }
  }

  delete = (text) => {
    console.log(text);
    this.props.deleteDomain(text.key)
  }

  edit = (record) => {
    console.log(record);
    this.setState({
      editting: record.key,
      enviromentValue: record.enviroment,
      domainValue: record.domain
    })
  }

  // onFinish = values => {
  //   console.log('Finish:', values);
  //   console.log(this.props)
  //   this.setState({
  //     enviromentValue: "",
  //     domainValue: ""
  //   })
  // };



  submitEdit = (record) => {
    console.log(this.state.enviromentValue);
    console.log(this.state.domainValue);
    this.props.editDomain({
      _id: record.key,
      enviroment: this.state.enviromentValue,
      domain: this.state.domainValue
    })
    this.setState({
      editting: "0"
    })
  }

  cancelEdit = (record) => {
    // console.log(record);
    this.setState({
      editting: "0"
    })
  }

  onChangeDomain = (e) => {
    // console.log(e.target.value)
    this.setState({ domainValue: e.target.value })
  }

  onChangeEnviroment = (e) => {
    // console.log(e.target.value)
    this.setState({ enviromentValue: e.target.value })
  }


  render() {

    // console.log(this.props.manageContent.domainList);
    const tableData = this.props.manageContent.domainList.map((element, index) => {
      return {
        key: (element._id).toString(),
        domain: element.domain,
        enviroment: element.enviroment
      }
    })
    return (
      <div className="domain_list">
        <WrapperForm partnerId={this.props.manageContent.partnerId} />
        <br />
        <br />

        <Table dataSource={tableData} pagination={false}>
          <Column
            title="Enviroment"
            dataIndex="enviroment"
            key="enviroment"
            width="300px"
            render={(text, record) => {
              // const domain = record.domain
              if (this.state.editting !== record.key) {
                // console.log(this.state.editting)
                // console.log(text)
                return text
              } else {
                return <Input value={this.state.enviromentValue} onChange={this.onChangeEnviroment} size="middle" />
              }
            }}
          />
          <Column
            title="Domain"
            dataIndex="domain"
            key="domain"
            width="400px"
            render={(text, record) => {
              // console.log(text)
              // const domain = record.domain
              if (this.state.editting !== record.key) {
                // console.log(this.state.editting !== record.key)
                // console.log(record.key)
                return <a href={text}>{text}</a>
              } else {
                return <Input value={this.state.domainValue} onChange={this.onChangeDomain} size="middle" />
              }
            }}
          />
          <Column
            title="Action"
            key="action"
            render={(text, record) => {
              if (this.state.editting !== text.key) {
                return (
                  <Space size="middle">
                    <a onClick={() => this.edit(record)}>Edit</a>
                    {/* <a onClick={() => this.delete(text)}>Delete</a> */}
                    <Popconfirm title="Sure to delete?" onConfirm={() => this.delete(text)}>
                      <a>Delete</a>
                    </Popconfirm>
                  </Space>
                )
              } else {
                return (
                  <Space size="middle">
                    <a onClick={() => this.submitEdit(record)}>Ok</a>
                    <a onClick={() => this.cancelEdit(record)}>Cancel</a>
                  </Space>
                )
              }
            }}
          />
        </Table>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  manageContent: state.manageContent,
});

const mapDispatchToProps = dispatch => ({
  getDomainList: (partnerId) => {
    dispatch(getDomainList(partnerId));
  },
  editDomain: (domain) => {
    dispatch(editDomain(domain));
  },
  deleteDomain: (id) => {
    dispatch(deleteDomain(id));
  },

});


export default connect(mapStateToProps, mapDispatchToProps)(DomainListTable);